export default {
	data() {
		return {
			login: {
				username:"",
				password:""
			},
			loading: false,
		};
	},
	methods:{
		defaultHandlerLogin:function(){
			this.loading = true;
			uni.request({
			    url: 'http://47.104.180.97:8080/miniApi/login', 
				method: "POST",
			    data: this.login,
			    success: (res) => {
					this.loading = false;
			        console.log(res.data);
			    },
				fail: (res) => {
					this.loading = false;
					console.log(res.data);
				}
			});
		},
		BindInput:function(e){
			var dataval = e.currentTarget.dataset.val;
			this.login[dataval] = e.detail.value; 
		},
		openregister:function(e){
			console.log(e)
			uni.navigateTo({
			    url: '../login/register',
			    success: res => {},
			    fail: () => {},
			    complete: () => {}
			});
		}
	}
}
